import React, { useEffect, useState } from 'react';
import { Link } from 'gatsby';
import { Navbar, Nav, Container, Offcanvas, NavDropdown } from 'react-bootstrap';
import { withPrefix } from 'gatsby';

const Header = () => {
    const [scrolled, setScrolled] = useState(false);
    const [showOffcanvas, setShowOffcanvas] = useState(false);

    let touchStartX = 0;

    const isMobileDevice = () => {
        return typeof window !== 'undefined' && window.matchMedia("(max-width: 575.98px)").matches;
    };

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 50) {
                setScrolled(true);
            } else {
                setScrolled(false);
            }
        };

        window.addEventListener('scroll', handleScroll);

        const handleTouchStart = (e) => {
            if (isMobileDevice()) {
                touchStartX = e.touches[0].clientX;
            }
        };

        const handleTouchEnd = (e) => {
            if (isMobileDevice()) {
                const touchEndX = e.changedTouches[0].clientX;
                const swipeDistance = touchEndX - touchStartX;
                const edgeThreshold = 30;
                const swipeThreshold = 50;

                if (touchStartX < edgeThreshold) {
                    if (swipeDistance > swipeThreshold) {
                        setShowOffcanvas(true);
                    }
                }
            }
        };

        if (typeof window !== 'undefined') {
            document.body.addEventListener('touchstart', handleTouchStart);
            document.body.addEventListener('touchend', handleTouchEnd);
        }

        return () => {
            window.removeEventListener('scroll', handleScroll);
            if (typeof window !== 'undefined') {
                document.body.removeEventListener('touchstart', handleTouchStart);
                document.body.removeEventListener('touchend', handleTouchEnd);
            }
        };
    }, []);

    const handleNavLinkClick = (e, targetId) => {
        e.preventDefault();
        const targetElement = document.getElementById(targetId);
        if (targetElement) {
            const headerHeight = document.getElementById('main-header').offsetHeight;
            const offset = 20;
            const elementPosition = targetElement.getBoundingClientRect().top + window.pageYOffset;
            const offsetPosition = elementPosition - headerHeight - offset;

            window.scrollTo({
                top: offsetPosition,
                behavior: "smooth"
            });

            setShowOffcanvas(false);
        }
    };

    const handleCloseOffcanvas = () => setShowOffcanvas(false);

    return (
        <header id="main-header" className={`fixed-top ${scrolled ? 'scrolled' : ''}`}>
            <Navbar bg="light" expand="lg" className="shadow-sm">
                <Container fluid>

                    {/* Botón para alternar el Offcanvas (menú móvil) - A la izquierda del logo en móvil */}
                    <Navbar.Toggle
                        aria-controls="offcanvasNavbar"
                        onClick={() => setShowOffcanvas(true)}
                        className="d-lg-none me-2" // Visible solo en móvil, margen a la derecha
                    />

                    {/* Navbar Brand (Logo/Título) - Visible siempre */}
                    {/* ms-auto aquí empuja el logo a la derecha en móvil para dejar espacio al toggle */}
                    <Navbar.Brand as={Link} to="/" className="me-lg-0 d-lg-none me-2">
                        <img src={withPrefix("/images/logo.png")} alt="Logo" width="30" height="30" className="d-inline-block align-text-top me-2" />
                        ArtMetrics
                    </Navbar.Brand>

                    
                    {/* Offcanvas para el menú móvil - Oculto en escritorio (d-lg-none) */}
                    <Navbar.Offcanvas
                        id="offcanvasNavbar"
                        aria-labelledby="offcanvasNavbarLabel"
                        placement="start"
                        show={showOffcanvas}
                        onHide={handleCloseOffcanvas}
                        className="d-lg-none"
                    >
                        <Offcanvas.Header closeButton>
                         
                        </Offcanvas.Header>
                        <Offcanvas.Body>
                            <Nav className="flex-grow-1 pe-3">
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'home')} href="#home">HOME</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'description')} href="#abstract">DESCRIPTION</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'location')} href="#location">LOCATION</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'call')} href="#call">CALL FOR PAPERS</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'dates')} href="#dates">IMPORANTE DATES</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'keynotes')} href="#keynotes">KEYNOTES</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'organisers')} href="#organisers">ORGANISERS</Nav.Link>
                               
                                 <NavDropdown title="PREVIOUS EDITIONS" id="offcanvas-previous-editions-nav-dropdown">
                                    <NavDropdown.Item as={Link} to="/" onClick={handleCloseOffcanvas}>2025 Edition (current)</NavDropdown.Item>
                                    <NavDropdown.Item as={Link} to="/edition-2024" onClick={handleCloseOffcanvas}>2024 Edition</NavDropdown.Item>
                                </NavDropdown>
                            </Nav>
                        </Offcanvas.Body>
                    </Navbar.Offcanvas>

                    {/* Navbar.Collapse para el menú de escritorio - Visible solo en 'lg' y superiores */}
                    {/* Le damos flex-grow-1 para que ocupe el espacio disponible y el mx-auto funcione */}
                    <Navbar.Collapse id="basic-navbar-nav" className="d-none d-lg-block flex-grow-1">
                        {/* El Nav que queremos centrar */}
                        <Nav className="mx-auto"> {/* ¡Clave! mx-auto centra este Nav dentro de su padre (Navbar.Collapse) */}
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'home')} href="#home">HOME</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'description')} href="#description">DESCRIPTION</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'location')} href="#location">LOCATION</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'call')} href="#call">CALL FOR PAPERS</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'dates')} href="#dates">IMPORANTE DATES</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'keynotes')} href="#keynotes">KEYNOTES</Nav.Link>
                                <Nav.Link onClick={(e) => handleNavLinkClick(e, 'organisers')} href="#organisers">ORGANISERS</Nav.Link>
                               
                                <NavDropdown title="PREVIOUS EDITIONS" id="basic-nav-dropdown">
                                    {/* Importante: Usar NavDropdown.Item con `as={Link}` y `to` para la navegación de Gatsby */}
                                    <NavDropdown.Item as={Link} to="/" >2025 Edition (current)</NavDropdown.Item>
                                    <NavDropdown.Item as={Link} to="/edition-2024">2024 Edition</NavDropdown.Item>
                                    {/* Puedes añadir un divisor si lo necesitas: <NavDropdown.Divider /> */}
                                </NavDropdown>
                        </Nav>
                    </Navbar.Collapse>

                </Container>
            </Navbar>
        </header>
    );
};

export default Header;