import React from 'react';
import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet'; 

import 'bootstrap/dist/css/bootstrap.min.css';
import '../styles/global.css'; 

import Header from './header';
import Footer from './footer';

const Layout = ({ children }) => {
    return (
        <>
            <Helmet>
                <html lang="es" />
                <title>ArtMetrics</title>
                <meta name="description" content="Biometrics for Art (ArtMetrics)" />
                <meta name="keywords" content="heritage biometrics, computer vision on multimodal imaging, computational imaging"/>
                <link rel="icon" type="image/x-icon" href="../artmetrics/images/favicon/favicon.ico" />
                <link rel="icon" type="image/x-icon" href="../artmetrics/images/favicon/apple-touch-icon.png" />
                <link rel="icon" type="image/x-icon" href="../artmetrics/images/favicon/favicon-16x16.png" />
                <link rel="icon" type="image/x-icon" href="../artmetrics/images/favicon/favicon-32x32.png" />
                <link rel="manifest" href="../artmetrics/images/favicon/site.webmanifest" /> 
            </Helmet>
            <Header />
            <main>{children}</main>
            <Footer />
        </>
    );
};

Layout.propTypes = {
    children: PropTypes.node.isRequired,
};

export default Layout;